.page-header{

    z-index:1000;
    position:sticky;
    height:0px;
    top :0;
    left:0;
    right:0;
    .navigation-bar{
        width: 100%;
        position:absolute;
        display: flex;
        justify-content: space-between;
        padding: 10px 0px;
        background-color:none;
        .logo{
            margin: 0px 30px 0px 0px;
            padding: 0px 15px;
            float:left;
        }
        .navigation-bar-item{
          width:80%;
          .navbar {
                overflow: hidden;
                background-color:none;
                a {
                  float: left;
                  font-size: 16px;
                  text-align: center;
                  padding: 14px 16px;
                  text-decoration: none;
                  font-weight:bold;
                  color: #253237;
                }
                .navbar-ul{
                    width:70%;
                }
                ul {
                  list-style-type: none;
                  margin: 0;
                  padding: 0;
                  overflow: hidden;
                  padding : 0px 15px;
                  li {
                      float: left;
                      a {
                          display: flex;
                          color:map-get($color,text-black);
                          text-align: center;
                          padding: 14px 16px;
                          text-decoration: none;
                          &:hover:not(.active) {
                              background-color:map-get($color,home-hover);
                          }
                      }
                  }
              }
                .dropdown {
                  float: left;
                  overflow: hidden;
                }
                .dropdown .dropbtn {
                  font-size: 16px;  
                  border: none;
                  outline: none;
                  padding: 14px 16px;
                  background-color: inherit;
                  font-family: inherit;
                  margin: 0;
                  font-weight:bold;
                  color: #253237;
                } 
                .navbar a:hover, .dropdown:hover .dropbtn {
                  background-color: none;
                }
                .dropdown-content {
                  display: none;
                  position: absolute;
                  background-color: #f9f9f9;
                  min-width: 160px;
                  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                  z-index: 1;
                  li {
                    float : unset;
                  }
                  top:80px;
                  a {
                    float: none;
                    color: black;
                    padding: 12px 16px;
                    text-decoration: none;
                    display: block;
                    text-align: left;
                  } 
                }
                .dropdown-content a:hover {
                  background-color: #ddd;
                }
                
                .dropdown:hover .dropdown-content {
                  display: block;
                }
                
          }           
        }
        .navbar-right-side{
          width:5%;
          display:flex;
          justify-content: space-between;
          margin-right: 20px;
        }
    }
}
